ity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}

.app-home{display:grid;grid-template-areas:'bio highlights' 'bio skills' 'experience experience' 'education education';grid-template-columns:48% 48%;justify-content:space-around}.app-home .home-card{margin:10px 5px;padding:5px}.app-home .home-card h3{margin:4px 0}.app-home .bio-card{grid-area:bio;padding-top:10px}.app-home .bio-card .media-area{height:200px;width:200px;margin:0 auto;border-radius:50%;box-shadow:inset 0 1.5px 3px 1px rgba(0,0,0,0.25),0 1.5px 3px 2px rgba(0,0,0,0.25)}.app-home .bio-card .card-buttons{display:grid;grid-template-columns:auto auto}.app-home .highlights-card{grid-area:highlights}.app-home .highlights-card ul{display:grid;grid-template-columns:50% 50%}.app-home .highlights-card ul a{text-decoration:none;display:list-item}.app-home .highlights-card ul a:hover{background-color:rgba(0,0,0,0.04)}.app-home .highlights-card ul .MuiListItem-root.MuiListItem-gutters{display:none}.app-home .highlights-card ul .MuiListItem-root.MuiListItem-gutters.linkset{display:block;text-align:center}.app-home .highlights-card .linkset img{width:150px;height:150px;border-radius:50%;box-shadow:inset 0 1.5px 3px 1px rgba(0,0,0,0.25),0 1.5px 3px 2px rgba(0,0,0,0.25);margin-bottom:5px 0}.app-home .skills-card{grid-area:skills}.app-home .skills-card ul{display:grid;grid-template-columns:25% 25% 25% 25%}.app-home .experience-card{grid-area:experience}.app-home .experience-card h3,.app-home .education-card h3{text-align:left}.app-home .experience-card li,.app-home .education-card li{padding-top:2px;padding-bottom:2px}.app-home .experience-card li span,.app-home .education-card li span{line-height:1.4}.app-home .experience-card .group{display:grid;grid-template-areas:'title title dates' 'description description description' 'jobTitle jobTitle jobTitle';margin:10px auto}.app-home .home-card .group .title{grid-area:title}.app-home .home-card .group .dates{grid-area:dates;text-align:right}.app-home .experience-card .group .description{grid-area:description;font-style:italic}.app-home .experience-card .group .job-title{grid-area:jobTitle}.app-home .experience-card .group .job-title span{font-weight:bold}.app-home .experience-card .group .skills{grid-column-start:3;grid-column-end:4;grid-row-start:4;grid-row-end:7}.app-home .home-card .group .skills .skill{margin:2px 10px}.app-home .experience-card .group .group-text{grid-column-start:1;grid-column-end:3}.app-home .experience-card .group-set-content{height:50vh}.app-home .home-card .group-set-nav{display:grid;grid-template-columns:50% 50%}.app-home .home-card .group-set-nav button{width:150px}.app-home .education-card{grid-area:education}.app-home .education-card .group{display:grid;grid-template-areas:'title title dates';grid-template-columns:33% 33% 33%;margin:10px auto;align-content:center}.app-home .education-card .group .group-text{grid-column-start:1;grid-column-end:3}.app-home .education-card .group .skills{grid-column-start:3;grid-column-end:4;grid-row-start:3;grid-row-end:7}.app-home .education-card .group-set-content{height:30vh}

/* Syntax highlighting */
.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #596267;
}
.token.punctuation {
  color: #9e9e9e;
}
.namespace {
  opacity: 0.7;
}
.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #e91e63;
}
.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #00914c;
}
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #795548;
}
.token.atrule,
.token.attr-value,
.token.keyword {
  color: #039be5;
}
.token.function {
  color: #5616d9;
}
.token.regex,
.token.important,
.token.variable {
  color: #ff9800;
}
.token.important,
.token.bold {
  font-weight: bold;
}
.token.italic {
  font-style: italic;
}
.token.entity {
  cursor: help;
}

.app-code-viewer {
  width: 60%;
  margin: 0 auto;
}
.xkcd .loading-logo {
  height: 70vh;
}

.slideshow {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 32% 32% 32%;
  height: 74vh;
  margin: 0 5px;
}

.panel-card {
  height: 70vh;
}

.slideshow img {
  max-width: 100%;
  max-height: 50vh;
}

.xkcd-footer nav {
  display: grid;
  justify-content: space-evenly;
  grid-template-columns: 23% 23% 23% 23%;
}

.xkcd-footer nav button {
  width: 30%;
  margin: 0 auto;
  text-align: center; 
  height: 7vh; 
  font-size: 17px;
  font-weight: bold;
  background-color: #6E7B91;
  color: white;
  cursor: pointer;
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2),0px 2px 2px 0px rgba(0,0,0,0.14),0px 1px 5px 0px rgba(0,0,0,0.12);
  border-radius: 7px;
  border: 0;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.credit {
  padding-top: 20px;
  font-size: 17px;
}
.app-portfolio{display:grid;grid-template-columns:99%;justify-content:space-around}.app-portfolio .portfolio-card{-webkit-animation:fade-in 0.5s linear;animation:fade-in 0.5s linear}.portfolio-display .slide{display:grid;justify-content:center;align-content:center;height:650px}.portfolio-display .slide img{max-height:600px;box-shadow:inset 0 1.5px 3px 1px rgba(0,0,0,0.25),0 1.5px 3px 2px rgba(0,0,0,0.25);margin-bottom:5px}.portfolio-display .slide .image-set{display:grid;grid-template-columns:auto auto;width:1300px}.portfolio-display button{background-color:#039be5;color:white;width:150px}.portfolio-display button:hover{background-color:#039be5}.portfolio-display div{background-color:white}.app-portfolio .portfolio-display .MuiMobileStepper-dot{background-color:#039be5}.app-portfolio .portfolio-display .MuiMobileStepper-dot.MuiMobileStepper-dotActive{background-color:blue}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}

